<template>
  <div>
    <h4>Habilidades</h4>
    <ul>
      <li class="badge badge-success m-1" v-for="(item, index) in items" v-bind:style="{ 'background-color' : item[2] }">{{ item[0] }}: {{ item[1] }} %</li>
    </ul>
  </div>
</template>

<script>
export default {
  props: ['items'],
  name: 'Skills'
}
</script>



