<template>
  <div>
    <h3>{{ item.job }} <small>({{ item.start }}-{{ item.ended }})</small></h3>
    <ul>
      <div v-for="company in item.companies">
        <h4>{{ company.name }}</h4>
        <p v-for="func in company.funcs" v-html="func"></p>
      </div>
    </ul>
  </div>
</template>

<script>
export default {
  props: ['item'],
  name: 'Job'
}
</script>



