<template>
  <span>
    <span v-if="lang == 'br'" v-html="strData[0]">
    </span>
    <span v-if="lang != 'br'" v-html="strData[1]">
    </span>
  </span>
</template>

<script>
import { i18nEmitter } from './../assets/js/i18nEmitter.js'
export default {
  props: ['strData'],
  name: 'i18n',
  data: function () {
    return {
      lang: i18nEmitter.currentLang
    }
  },
  mounted () {
    i18nEmitter.$on('langChange', newLang => {
      this.lang = newLang
    })
  }
}
</script>



