<template>
	<div>
    <Profile/>
		<div>
      <div class="text-justify m-1">
        <p><i18n :strData="[
        'Esta  a minha apresentao, uma breve descrio de tudo que tenho feito ao longo destes anos.',
        'This is my presentation, a brief description of everything I\'ve done over these past years.']"/></p>

        <p><i18n :strData="[
        'Abaixo voc vai poder conferir minhas habilidades e um coeficiente de sabedoria sobre a tecnologia.',
        'Below you be able to check my skills and a coefficient of knowledgement about the technology.']"/></p>
      </div>
      <div>
        <h4><span><i18n :strData="['Acompanhe meu progresso', 'Follow my progress']"/>: <span id="yearSliderValue"></span></span></h4>
        <div class="sliderArea">
          <p><div id="yearSlider" class="skillYearSlider"></div></p>
        </div>
      </div>
      <div class="row">
        <div class="col-md-6">
    			<canvas id="skillChart" width="600" height="400"></canvas>
        </div>
        <div class="col-md-6">
          <div id="skillsInfo"/>
        </div>
      </div>
      <div id="skillChartInfo"></div>
		</div>
    <Resume/>
    <Activities/>
  </div>
</template>

<script>
import Vue from 'vue'
import i18n from './i18n'
import Profile from './Profile'
import Resume from './Resume'
import Activities from './Activities'
import Jobs from './Jobs'
import Skills from './Skills'
import $ from 'jquery'
import './../assets/js/jquery-ui.min.js'
import AppControl from './../assets/js/app.js'
import { i18nEmitter } from './../assets/js/i18nEmitter.js'

var JobsCtorObj = null
var SkillsCtorObj = null

export default {
  components: {
    'i18n': i18n,
    'Profile': Profile,
    'Resume': Resume,
    'Activities': Activities
  },
  name: 'About',
  mounted () {
    // create component constructor
    const JobsCtor = Vue.extend(Jobs)
    JobsCtorObj = new JobsCtor({
      propsData: {
        jobs: []
      }
    }).$mount('#skillChartInfo')

    const SkillsCtor = Vue.extend(Skills)
    SkillsCtorObj = new SkillsCtor({
      propsData: {
        items: []
      }
    }).$mount('#skillsInfo')

    let comp = this
    $('#yearSlider').slider({
      range: true,
      min: 2001,
      max: 2017,
      values: [ 2001, 2017 ],
      lineTension: 2,
      slide: function (event, ui) {
        comp.updateCart(ui.values[0], ui.values[1])
        $(this).find('.ui-slider-handle:first').text(ui.values[0])
        $(this).find('.ui-slider-handle:last').text(ui.values[1])
      },
      create: function (event, ui) {
        $(this).find('.ui-slider-handle:first').text($(this).slider('values', 0))
        $(this).find('.ui-slider-handle:last').text($(this).slider('values', 1))
      }
    })
    this.updateCart($('#yearSlider').slider('values', 0), $('#yearSlider').slider('values', 1))

    i18nEmitter.$on('langChange', newLang => {
      this.updateCart($('#yearSlider').slider('values', 0), $('#yearSlider').slider('values', 1))
    })
  },
  data () {
    return {
      currDate: (new Date()).toString(),
      updateCart (initialYear, lastYear) {
        $('#yearSliderValue').text(i18nEmitter.i18n('De', 'From') + ' ' + initialYear + ' ' + i18nEmitter.i18n('at', 'to') + ' ' + lastYear)
        let yearData = AppControl.About.doChart('#skillChart', [initialYear, lastYear])
        let jobYearData = yearData.jobs

        let jobCompData = []
        for (let jobYearId in jobYearData) {
          let jobs = jobYearData[jobYearId].jobs
          for (let jobId in jobs) {
            let job = jobs[jobId]

            let jobComp = jobCompData.findIndex(x => ((x.job === job[0]) && (x.ended + 1 === jobYearData[jobYearId].year)))
            if (jobComp === -1) {
              let jobInfo = {
                job: job[0],
                start: jobYearData[jobYearId].year,
                ended: jobYearData[jobYearId].year,
                companies: [{ name: job[1][0], funcs: [job[1][1]] }]
              }
              jobCompData.push(jobInfo)
            } else {
              let jobInfo = jobCompData[jobComp]
              jobInfo.ended = jobYearData[jobYearId].year

              let companyId = jobInfo.companies.findIndex(x => (x.name === job[1][0]))
              if (companyId > -1) {
                if (jobInfo.companies[companyId].funcs.indexOf(job[1][1]) === -1) {
                  jobInfo.companies[companyId].funcs.push(job[1][1])
                }
              }
            }
          }
        }

        JobsCtorObj.jobs = jobCompData

        SkillsCtorObj.items = yearData.skills
      }
    }
  }
}
</script>



