import Vue from 'vue';
export const i18nEmitter = new Vue({
  created: function () {
    this.$on('langChange', newLang => {
      this.currentLang = newLang
    })
  },
  data: {
    currentLang: "br",
    i18n (a, b) {
      return (this.currentLang === 'br') ? a : b
    }
  }
});



