<template>
  <div>
    <h4><i18n :strData="['Trabalhos', 'Jobs']"/></h4>
    <div class="jobs m-1">
      <ul>
        <Job v-for="(item, index) in jobs" :item="item" :key="index"></Job>
      </ul>
    </div>
  </div>
</template>

<script>
import i18n from './i18n'
import Job from './Job'
export default {
  components: {
    'i18n': i18n,
    'Job': Job
  },
  props: ['jobs'],
  name: 'Jobs'
}
</script>



