<template>
  <div class="p-3 mx-auto flex-column app">
    <header class="header">
      <div class="row">

        <div class="col-md-6 title text-center">
          <img class="profileImg" width="135px" src="https://avatars1.githubusercontent.com/u/16818189?s=460&v=4" />
          <h1> Gilberto Saraiva </h1>
        </div>

        <div class="col-md-6">

          <div class="row">
            <div class="col-12 text-right">
              <router-link :to="{ name: 'Home' }" class="btn btn-sm btn-info">Home</router-link>
              <router-link :to="{ name: 'About' }" class="btn btn-sm btn-info"><i18n :strData="['Sobre mim', 'About me']"/></router-link>
              <router-link :to="{ name: 'Contact' }" class="btn btn-sm btn-info"><i18n :strData="['Contato', 'Contact']"/></router-link>
            </div>
          </div>

          <div class="row">   
            <div class="col-12 text-right">              
              <button @click="changeLanguage" id="btnLanguage" class="langFlag langEN"></button>
            </div>
          </div>

          <hr class="hr1"></hr>

          <div class="row">     
            <div class="col-12 text-right">     
              <h2><i18n :strData="['Desenvolvedor Snior', 'Senior Developer']"/></h2>
            </div>
          </div>

          <div class="row"> 
            <div class="col-12 social text-right">
              <a href="https://www.facebook.com/gilbertosaraiva"><span class="fa fa-facebook-square social-icon"></span></a>
              <a href="https://twitter.com/gsaraiva"><span class="fa fa-twitter-square social-icon"></span></a>
              <a href="https://github.com/saraivagilberto"><span class="fa fa-github-square social-icon"></span></a>
              <a href="https://plus.google.com/u/0/100811316712289045414/posts"><span class="fa fa-google-plus-square social-icon"></span></a>
              <a href="https://www.linkedin.com/comm/in/gsaraiva"><span class="fa fa-linkedin-square social-icon"></span></a>
              <a href="mailto:saraivagilberto@gmail.com"><span class="fa fa-envelope-square social-icon"></span></a>
            </div>   
          </div>       
        </div>
      </div>
    </header>

    <router-view class="content"/>
    <Footer />
  </div>
</template>

<script>
import $ from 'jquery'
import './assets/js/jquery-ui.min.js'
import './assets/js/bootstrap.bundle.min.js'
import './assets/js/app.js'

import { i18nEmitter } from './assets/js/i18nEmitter.js'
import i18n from './components/i18n'
import Footer from './components/Footer'

export default {
  components: {
    'i18n': i18n,
    'Footer': Footer
  },
  name: 'app',
  data: function () {
    return {
      lang: 'br'
    }
  },
  methods: {
    changeLanguage () {
      this.lang = (this.lang === 'br') ? 'en' : 'br'
      i18nEmitter.$emit('langChange', this.lang)
      if (this.lang === 'br') {
        $('#btnLanguage').removeClass('langBR')
        $('#btnLanguage').addClass('langEN')
      }

      if (this.lang === 'en') {
        $('#btnLanguage').removeClass('langEN')
        $('#btnLanguage').addClass('langBR')
      }
    }
  }
}
</script>

<style lang="scss">
@import './assets/css/app.scss';
</style>



